//Base
@import url("https://fonts.googleapis.com/css2?family=Glory:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&family=Inter:wght@100;200;500;600;700&family=Noto+Sans+Georgian:wght@300;400;500;600;800&family=Nunito:wght@200&display=swap");
$black: #000;
$bgnavtop: #fdc800;
$bgnavbar: #f0f4f9;
$navlogo: #002147;
$btnYellow: #fdc800;
$whitebtn: #ffffff;
$cardfont: #757575;

* {
    font-family: "Inter", sans-serif;
}
.header {
    // top navbar
    .navbar-top {
        background-color: $bgnavtop;
        padding-block: 12px;
        .contact-info {
            li {
                a {
                    font-size: 14px;
                    color: $black;
                    i {
                        padding-right: 10px;
                    }
                }

                &:not(:last-child) {
                    @media screen and (min-width: 768px) {
                        margin-right: 54px;
                    }
                }
            }
        }
        .social-icons {
            li {
                a {
                    font-size: 13px;
                    color: $black;
                }
                &:not(:last-child) {
                    margin-right: 24px;
                }
            }
        }
    }
    // End Navbar

    // start
    .navbar {
        // top: 0;
        // position: -webkit-sticky;
        // position: sticky;
        background-color: $bgnavbar;

        .navbar-brand {
            color: $navlogo;
        }
        .navbar-nav {
            padding-right: 100px;
            .nav-item {
                .nav-link {
                    color: $navlogo;
                    font-weight: bold;
                    padding-left: 20px;
                    padding-right: 20px;
                    &:hover {
                        background-color: $btnYellow;
                        border-radius: 2px;
                    }
                }
            }
        }
        .search {
            .btn1 {
                background-color: $btnYellow; /* Green */
                border: none;
                color: $black;
                text-align: center;
                text-decoration: none;
                display: inline-block;
                font-size: 16px;
                margin-right: 20px;
                border-radius: 4px;

                width: 100px;

                cursor: pointer;
                &:hover {
                    background-color: $black;
                    color: $whitebtn;
                }
            }
            // .sign-input{
            //     width: 44px;
            //     height: 44px;
            //     background-color: white;
            //     border: 1px solid  #BDBDBD;
            //     border-radius: 6px;
            //     display: flex;
            //     align-items: center;

            // }
            // i{
            //     font-size: 20px;
            //     color: $black;
            //     flex: 1;
            // }

            .has-search .form-control {
                padding-left: 1.375rem;
                margin-top: 3px;
                width: 44px;
                height: 44px;
            }

            .has-search .form-control-feedback {
                position: absolute;
                z-index: 2;
                display: block;
                width: 2.375rem;
                height: 2.375rem;
                line-height: 2.375rem;
                text-align: center;
                pointer-events: none;
                color: $black;
            }
        }
    }
}
